@import "https://fonts.googleapis.com/css2?family=Inter&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  font-family: Inter, sans-serif;
  bottom: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

body.menu__show {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.icon__green-beacon {
  fill: #01b401;
}

.main {
  padding: 0 8px;
}

.nav {
  height: 64px;
  width: 100%;
  border-bottom: 1px solid #d3d3d3;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  display: flex;
  box-shadow: 0 1px 3px #d3d3d3;
}

.navbar__brand {
  align-items: center;
  gap: 8px;
  font-size: 1.25rem;
  display: flex;
}

.btn__menu {
  cursor: pointer;
  background: none;
  border: none;
}

.icon__menu.menu__show, .icon__close {
  display: none;
}

.icon__close.menu__show {
  display: block;
}

.btn__menu__spacer {
  height: 64px;
  width: 100%;
  justify-content: end;
  padding: 8px;
  display: flex;
}

.menu {
  width: 100vw;
  height: calc(100vh - 64px);
  background-color: #fff;
  padding: 8px;
  display: none;
  position: absolute;
  top: 64px;
  left: 0;
}

.menu.menu__show {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.menu__items {
  flex-direction: column;
  gap: 25px;
  list-style: none;
  display: flex;
}

.menu__items > .menu__item {
  width: 100%;
  border: 1px solid #01b401;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  padding: 8px 12px;
  display: flex;
}

